import React, { useState } from 'react'
import PropTypes from 'prop-types'
import StyledEditProfile, { GroupTitle, GenderAndRegion, SelectGrop, StyledIconInput }  from './style'
import Profile from 'components/Profile'
import Avatar from 'components/Avatar'
import Button from 'components/Button'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCheck } from '@fortawesome/free-solid-svg-icons'
import face3 from "assets/images/face-male-3.jpg"
import "styled-components/macro"
import InputText from 'components/Input/InputText'
import Radio from 'components/Radio'
import LabelContainer from 'components/LabelContainer'
import Select from 'components/Select'
import Option from 'components/Option'
import Icon from 'components/Icon'
import { faWeibo, faGithub, faLinkedin } from '@fortawesome/free-brands-svg-icons'
function EditProfile({
    children,
  ...rest }) {
  const [showEdit, setShowEdit] = useState(false)
  if (!showEdit) {
    return <Profile 
      onEdit={() => setShowEdit(true)}
      showEditBtn
      showCloseIcon={false}
    />
  }
    return (
        <StyledEditProfile{...rest}>
        <Avatar src={face3} size="160px" css={`
          grid-area: 1/1/2/2;
          justify-self: center;
          margin-bottom: 12px;
        `} />
        <Button size="52px" css={`
          grid-area: 1/1/3/2;
          justify-self: end;
          align-self: end;
          z-index: 10;
        `} >
          <FontAwesomeIcon icon={faCheck} onClick={() => setShowEdit(false)} />
        </Button>
        <GroupTitle></GroupTitle>
        <InputText label="" />
        <GenderAndRegion>
          <Radio.Group label="">
            <Radio name="gender"></Radio>
            <Radio name="gender"></Radio>
          </Radio.Group>
          <LabelContainer label="">
            <SelectGrop>
              <Select type="form">
                <Option></Option>
                <Option></Option>
                <Option></Option>
                <Option></Option>
              </Select>
              <Select type="form">
                <Option></Option>
                <Option></Option>
                <Option></Option>
              </Select>
              <Select type="form">
                <Option></Option>
              </Select>
            </SelectGrop>
          </LabelContainer>
        </GenderAndRegion>
        <InputText label="" />

        <GroupTitle></GroupTitle>
        <InputText label="" />
        <InputText label="" />
        <InputText label="" />
        <GroupTitle></GroupTitle>
        <IconInput icon={faWeibo} bgColor="#F06767" />
        <IconInput icon={faGithub} bgColor="#000" />
        <IconInput icon={faLinkedin} bgColor="#2483C0" />
        </StyledEditProfile>
    )
}
function IconInput({ icon, bgColor, ...rest }) {
  return (
    <StyledIconInput>
      <Icon.Social icon={icon} bgColor={bgColor} />
      <InputText {...rest} />
    </StyledIconInput>
  )
}
EditProfile.propTypes = {
  children:PropTypes.any,
}

export default EditProfile