import React from 'react'
import PropTypes from 'prop-types'
import StyledConversation, {Conversations, MyChatBubble} from './style'
import TitleBar from 'components/TitleBar'
import VoiceMessage from 'components/VoiceMessage'
import Footer from 'components/Footer'
import ChatBubble from 'components/ChatBubble'
import Emoji from 'components/Emoji'
import { useSpring } from 'react-spring'
/* eslint-disable jsx-a11y/accessible-emoji */
// 
function Conversation({
  onAvatarClick,
  onVideoClick,
    children,
  ...rest }) {
  // titleBr
  const tBarAnimeProps = useSpring({
    opacity: 1,
    transform: "translate3d(0px, 0px, 0px)",
    from: { opacity: 0, transform: "translate3d(0px, -50px, 0px)" },
    delay: 500,
  })
// 
  const convsAnimeProps = useSpring({
    opacity: 1,
    transform: "translate3d(0px, 0px, 0px)",
    from: { opacity: 0, transform: "translate3d(50px, 0px, 0px)" },
    delay: 800,
  })
  // 
  const ftAnimeProps = useSpring({
    opacity: 1,
    transform: "translate3d(0px, 0px, 0px)",
    from: { opacity: 0, transform: "translate3d(0px, 50px, 0px)" },
    delay: 950,
  })
    return (
      <StyledConversation{...rest}>
        <TitleBar
          onAvatarClick={onAvatarClick}
          onVideoClick={onVideoClick}
          animeProps = {tBarAnimeProps}
        />
        <Conversations style={convsAnimeProps}>
          <ChatBubble time=" 14:27">Hi</ChatBubble>
          <MyChatBubble time=" 14:30">Hello</MyChatBubble>
          <ChatBubble time=" 14:40">
            <VoiceMessage time="0:42"></VoiceMessage>
          </ChatBubble>
          <MyChatBubble time=" 14:45"><Emoji label="smileWithTears"></Emoji></MyChatBubble>
          <ChatBubble time=" 14:47">
            <VoiceMessage time="0:20"></VoiceMessage>
          </ChatBubble>
          <MyChatBubble time=" 14:49">
          <VoiceMessage time="0:12" type="mine"></VoiceMessage>
          </MyChatBubble>
          <ChatBubble time=" 14:50"></ChatBubble>
          <MyChatBubble time=" 14:55"><Emoji label="oneEyeClosed"></Emoji></MyChatBubble>
        </Conversations>
        <Footer animeProps={ftAnimeProps}/>
        </StyledConversation>
    )
}
Conversation.propTypes = {
  children:PropTypes.any,
}

export default Conversation